.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 200px;
		transform: translate(-30%, -40%);
		@include max-md {
			width: 160px;
		}
		img {
			transform-origin: 60% 90%;
		}
	}
	.stream-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		z-index: 5;
		.background-full {
			left: unset;
			width: 60%;
			@include max-lg {
				width: 100%;
				height: 80%;
				top: unset;
				&.has-animate {
					animation-name: heightDown;
				}
			}
		}
		.stream-content {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 0;
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}
