.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		right: 0;
		bottom: 0;
		width: 200px;
		transform: translateX(45%) rotate(25deg);
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 60% 90%;
		}
	}
	.background-element {
		transform: scaleX(-1);
	}
	.image-wrapper {
		position: relative;
		height: 100%;
		width: 100%;
		margin: 0 auto;
		border-radius: var(--border-radius);
		@include max-md {
			height: auto;
			aspect-ratio: 5/6;
		}
		.rsvp-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: var(--border-radius);
		}
	}
	.rsvp-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		overflow: hidden;
		.background-full {
			@include max-md {
				&.has-animate {
					animation-name: heightDown;
				}
			}
		}
		.rsvp-form {
			padding: 2rem;
			position: relative;
			@include max-md {
				padding: 2rem 1rem 1rem;
			}
		}
	}
}
