.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		left: 45%;
		top: 0;
		width: 200px;
		transform: translate(-50%, -50%);
		@include max-lg {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 60% 90%;
		}
	}
	.story-content {
		position: relative;
		border-radius: var(--border-radius);
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
		.background-full {
			left: unset;
			width: 70%;
			@include max-lg {
				width: 100%;
				&.has-animate {
					animation-name: heightDown;
				}
			}
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		padding: 2rem;
		@include max-md {
			padding: 2rem 1rem;
		}
		* {
			position: relative;
		}
	}
	.image-story-wrap {
		position: relative;
		width: 100%;
		aspect-ratio: 4/3;
		border-radius: var(--border-radius);
		z-index: 3;
		@include max-lg {
			&.has-animate {
				animation-name: none;
			}
		}
		.story-image {
			object-fit: cover;
			width: 100%;
			height: 100%;
			border-radius: var(--border-radius);
		}
	}
}
