.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.elements-absolute {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 90%;
		.background-element {
			display: block;
			height: 100%;
			.background-wrapper {
				width: 100%;
				height: 100%;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		transform: translate(-40%, 15%);
		width: 240px;
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			width: 180px;
		}
		@include max-xs {
			width: 160px;
		}
		img {
			transform-origin: 60% 90%;
		}
	}
	.and {
		@include min-md {
			display: none;
		}
		font-weight: 600;
		color: var(--color-dark);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 50px;
		aspect-ratio: 1;
		position: relative;
		z-index: 3;
		span {
			position: relative;
			font-size: 24px;
			font-family: $font-secondary;
		}
	}
	.couple-content {
		position: relative;
		border-radius: var(--border-radius);
		padding: 2rem;
		background-color: var(--color-primary);
		@include max-md {
			padding: 2rem 1rem;
		}
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 1rem;
		border-radius: 12px;
		overflow: hidden;
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 9/10;
			border-radius: var(--border-radius);
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				border-radius: var(--border-radius);
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 1rem;
		}
		.couple-parent {
			width: 100%;
			margin: auto;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-button-trans);
				text-decoration: none;
				padding: 0.5rem 1.5rem;
				border-radius: 50px;
				background: linear-gradient(var(--color-light), var(--color-light)) padding-box, var(--color-gradient) border-box;
				border: 1px solid transparent;
				transition: all 0.25s ease;
				display: block;
				width: fit-content;
				margin: 0 auto;
				&:hover {
					background: var(--color-secondary);
					color: var(--text-button);
				}
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
