.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 50%;
		transform: translate(-50%, -40%);
		width: 200px;
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			width: 160px;
		}
		img {
			transform-origin: 60% 90%;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 6/4;
		overflow: hidden;
		border-radius: var(--border-radius);
		@include max-md {
			aspect-ratio: 5/6;
		}
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: var(--border-radius);
		}
	}
	.thank-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		overflow: hidden;
		.background-full {
			@include max-md {
				&.has-animate {
					animation-name: heightUp;
				}
			}
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
