.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		@include max-lg {
			padding: 4rem 0;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			.cover-body {
				overflow: hidden;
				border-radius: 12px;
				position: relative;
				.image-wrapper {
					position: relative;
					width: 100%;
					aspect-ratio: 6/4;
					border-radius: 12px;
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 12px;
					}
				}
				.cover-description {
					padding: 1.5rem 1.5rem 2rem;
					text-align: center;
				}
			}
			.elements-widget {
				width: 100%;
				.btn-custom {
					transform: translateY(-50%);
				}
			}
		}
	}
}
