.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	.scroll-icon {
		position: absolute;
		top: 50%;
		right: 0;
		transform: translate(50%, -50%);
		z-index: 5;
		@include max-md {
			top: 100%;
			right: 50%;
		}
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 240px;
		transform: translate(50%, 0) scaleX(-1) rotate(-15deg);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			width: 140px;
		}
		img {
			transform-origin: 60% 90%;
		}
	}
	.elements-absolute {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.background-element {
			display: block;
			height: 100%;
			.background-wrapper {
				width: 100%;
				height: 100%;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 6/4;
		overflow: hidden;
		border-radius: 12px;
		@include max-lg {
			aspect-ratio: 1;
		}
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 12px;
		}
	}
	.header-content {
		position: relative;
		text-align: left;
		border-radius: 12px;
		.background-full {
			@include max-md {
				&.has-animate {
					animation-name: heightUp;
				}
			}
		}
		.header {
			position: relative;
			width: 100%;
			height: 100%;
			padding: 3rem;
			@include max-md {
				padding: 2rem;
			}
			.header-title {
				position: relative;
				display: flex;
				flex-direction: column;
				justify-content: space-between;
				width: 100%;
				height: 100%;
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
