.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		right: 0;
		bottom: 2rem;
		width: 200px;
		transform: translateX(45%) rotate(25deg);
		@include max-md {
			width: 160px;
		}
		img {
			transform-origin: 60% 90%;
		}
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		background-color: var(--color-primary);
		border-radius: var(--border-radius);
		@include max-md {
			padding: 2rem 1rem;
		}
	}
}
